* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
  font-size: 20px;
}




.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  color: #222;
}

header {
  flex-basis: 30vh;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}

aside {
  flex-basis: 30%;
}

section {
  flex-grow: 1;
}

footer {
  background-color: black;
  color: white;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
}
header {
  position: relative;
  overflow: hidden; 
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-height: 100%;
  min-width: 100%;
}
nav.main {
  padding: 0 30px 30px 0;
}

.main ul {
  list-style: none;

}

nav.main a {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  transition: .2s;
}

nav.main a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

nav.main a.active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  
}

nav.main li:nth-child(1) a.active {
  border-left: green 10px solid;
  
}

nav.main li:nth-child(2) a.active {
  border-left: red 10px solid;
  ;
}

nav.main li:nth-child(3) a.active {
  border-left: blue 10px solid;
  
}

nav.main li:nth-child(4) a.active {
  border-left: yellow 10px solid;
  
}
.products ul {
  list-style: none;
}
.contact textarea {
  display: block;
  width: 80%;
  font-size: 16px;
  min-height: 200px;
  margin: 20px 0;
}

.contact button {
  display: block;
  border: 2px solid black;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 20px;
  margin: 0 20% 0 auto;
  transition: .2s;
}

.contact button:hover {
  color: yellow;
background-color: black;
}
footer span {
  color: red;
}
